.page-search {
  border-radius: 5px;
  margin: 0 auto;
  max-width: 350px;
  min-height: 40px;
  overflow: hidden;
  padding: 1px 4px;
  min-width: 360px;
  background-color: #f2f4f6;
  display: flex;
  gap: 5px;
}
.page-search input {
  width: calc(100% - 35px);
  border: none;
  padding-left: 35px;
  background-image: url("../../../images/particals/search/search.svg");
  background-repeat: no-repeat;
  background-size: 19px;
  background-position: left;
  background-position-x: 6px;
  font-size: 15px;
  background-color: inherit;
}
.page-search input::placeholder {
  color: #666666d6;
}
.page-search .clear {
  align-self: center;
  min-width: 8px;
}
.page-search .clear span {
  display: none;
  cursor: pointer;
  user-select: none;
}
.page-search .clear span.active {
  display: block;
}
.page-search button {
  width: 70px;
  user-select: none;
  cursor: pointer;
  border: none;
  background: none;
  font-size: 1em;
  font-family: "Bold";
  transition: 0.4s;
  color: #444444b3;
}
.page-search button:hover {
  color: #0082C7;
}

@media (max-width: 490px) {
  .page-search {
    min-width: calc(100% - 8px);
    max-width: calc(100% - 8px);
  }
  .page-search input::placeholder {
    font-size: 0.9em;
  }
  .page-search button {
    display: none;
  }
}

